@charset 'utf-8';

@import url('https://fonts.googleapis.com/css2?family=Nanum+Gothic&family=Nanum+Gothic+Coding&display=swap');

* { font-family: 'Nanum Gothic', sans-serif; }
h2 { font-size: 30px; }
h3 { font-size: 20px; }
h4 { font-size: 15px; }
p { font-size: 14px; }
a, a:active { color : black; cursor: pointer; }
a:hover { transition: 0.5s; }
a.man:hover { color: skyblue; }
a.woman:hover { color: pink; }
a img {  transition: 0.5s; transform: scale(1); } /* 마우스 Hover 이벤트 시 이미지 축소 애니메이션을 위한 CSS */
a:hover img { transition: 0.5s; transform: scale(1.2); }
header ul { display: flex;  }
.click { color: skyblue; }
button { color: skyblue; background-color: rgba(0, 0, 0, 0); border: 1px dotted skyblue; margin: 2px; padding: 5px; cursor: pointer; }
button.man { color: skyblue; border: 1px dotted skyblue; }
button.woman { color: pink; border: 1px dotted pink; }